export const ThemeProvider: React.FC<{ theme: typeof DefaultTheme }> = ({
  children,
  theme,
}) => {
  return (
    <EmotionProvider theme={theme}>
      <BaseLine theme={theme}>{children}</BaseLine>
    </EmotionProvider>
  );
};

export type Theme = typeof DefaultTheme;

export const useTheme = () => useEmotionTheme<typeof DefaultTheme>();
