import React, { useState } from "react";
import { styled } from "../../utils/theme/styled";
import Button from "../Button";
// Contains the buttons and the slider
// Keeps them all 100% height and in line
// with each other 👌
const CarouselContainer = styled.div`
  display: flex;
  flex-direction: row;
  margin-left: auto;
  margin-right: auto;
  margin-top: 1.5em;
  margin-bottom: 1.5em;
  flex-direction: column;
`;

// Wraps the slider to hide the inactive slides
const SliderOuter = styled.div`
  overflow: hidden;
  height: ${(props: any) => props.height}px;
  width: ${(props: any) => props.width}px;
`;

// Is the width of all the images and
// sets it's left position -((carousel_width * current_index) - carousel_width)
// - 1 width because we're 1 indexed so we don't have to worry about * 0
const SliderInner = styled.div`
  display: block;
  position: relative;
  width: ${(props: any) => props.children.length * props.width}px;
  left: -${(props: any) => props.width * props.index - props.width}px;
  transition: left 1s;
  > img {
    float: left;
  }
`;
export type Props = {
  initialHeight?: number;
  initialWidth?: number;
  initialIndex?: number;
};
const Carousel: React.FC<Props> = ({
  children,
  initialHeight = "100%",
  initialIndex = 0,
  initialWidth = "100%",
}) => {
  const [{ width, height, index }, setState] = useState({
    width: initialWidth,
    height: initialHeight,
    index: initialIndex,
  });
  const next = () => {
    // Set the index to the next index or the first index
    setState((prevState) => {
      const nextIndex = prevState.index + 1;
      return {
        ...prevState,
        index: nextIndex > React.Children.count(children) ? 1 : nextIndex,
      };
    });
  };

  const prev = () => {
    // Set the index to the prev index or the last index
    setState((prevState) => {
      const nextIndex = prevState.index - 1;
      return {
        ...prevState,
        index: nextIndex < 1 ? React.Children.count(children) : nextIndex,
      };
    });
  };

  // const onResize = () => {
  //   // Update width and height
  // };

  // const onLoad = () => {
  //   // Maybe update width and height when the contained images have loaded
  // };

  // const imgSize = () => {
  //   // Take an image and the desired width/height
  //   // Return a width+height that fits the contraints
  //   // but maintains the aspect ratio of the original image
  // };

  return (
    <CarouselContainer>
      <SliderOuter width={width} height={height}>
        <SliderInner width={width} index={index}>
          {React.Children.map(children, (el: any, i) => (
            <img
              key={`img${i}`}
              src={el.props.src}
              style={{
                width,
                height,
              }}
            />
          ))}
        </SliderInner>
      </SliderOuter>
      <div>
        <Button onClick={prev}>Previous</Button>
        <Button onClick={next}>Next</Button>
      </div>
    </CarouselContainer>
  );
};

export default Carousel;
