import { css } from "@emotion/core";
import DefaultTheme from "../../../../utils/theme/defaultTheme";

const TextFieldStyled = ({
  prefix,
  palette,
  transitions,
}: typeof DefaultTheme) => css`
  .${prefix}-textfield {
    --textfield-helper-color: ${palette.text.secondary};
    --textfield-color: ${palette.text.primary};
    --textfield-active-color: ${palette.primary.main};
    --textfield-transitions: ${transitions.duration.shortest}ms
      ${transitions.easing.easeInOut};
    --text-field-border-color: #00000061;
    --text-field-input-base-padding: 12px 16px 14px;
    --text-field-adornment-start-width: 8px;
    --text-field-adornment-end-width: 8px;
    position: relative;
    justify-content: space-between;
    border-radius: 4px 4px 0 0;
    position: relative;
    display: inline-flex;
    align-content: center;
    align-items: center;
    box-sizing: border-box;
    will-change: opacity, transform, color;
    transition: all 0.2s linear;
    border-radius: 4px;

    .${prefix}-input-base {
      padding: var(--text-field-input-base-padding);
      border: none !important;
      outline: none;
      background-color: #0000;
      z-index: 1;
      caret-color: #6200ee;
      -moz-osx-font-smoothing: grayscale;
      -webkit-font-smoothing: antialiased;
      font-size: 1rem;
      font-weight: 400;
      letter-spacing: 0.009375em;
      text-decoration: inherit;
      text-transform: inherit;
      display: flex;
      align-self: flex-end;
      box-sizing: border-box;
      width: 100%;
      height: 100%;
      transition: opacity 0.15s cubic-bezier(0.4, 0, 0.2, 1);
      border: none;
      border-radius: 0;
      background: none;
      appearance: none;
      &:focus + .${prefix}-textfield-notched-outline {
        .${prefix}-textfield-notched-outline-label {
          border-top-color: transparent;
          label {
            transform: translateY(-110%) scale(0.75);
          }
        }
      }
    }

    &-notched-outline {
      display: flex;
      position: absolute;
      right: 0;
      left: 0;
      -webkit-box-sizing: border-box;
      box-sizing: border-box;
      width: 100%;
      max-width: 100%;
      height: 100%;
      text-align: left;
      pointer-events: none;
      &-leading {
        border-radius: 4px 0 0 4px;
        border: 1px solid var(--text-field-border-color);
        border-right: none;
        pointer-events: none;
        min-width: 8px;
        width: var(--text-field-adornment-start-width, 8px);
      }
      &-label {
        flex: 0 0 auto;
        width: auto;
        max-width: calc(100% - 12px * 2);
        height: 100%;
        border-top: 1px solid var(--text-field-border-color);
        border-bottom: 1px solid var(--text-field-border-color);
        pointer-events: none;
        display: inline-flex;
        align-items: center;
        padding: var(--text-field-input-base-padding);
        label {
          transition: transform 0.2s ease-in-out;
          color: rgba(var(--textfield-color), 0.6);
          display: inline-block;
          position: relative;
          max-width: 100%;
        }
      }
      &-trailing {
        flex-grow: 1;
        border-radius: 0 4px 4px 0;
        border: 1px solid var(--text-field-border-color);
        border-left: none;
        height: 100%;
        pointer-events: none;
        min-width: 8px;
        width: var(--text-field-adornment-end-width, 8px);
      }
    }
  }
`;

export default TextFieldStyled;
