import React, { useEffect, useState } from "react";
import styled from "@emotion/styled";
import { useTheme } from "../../utils/theme/theming";
import {
  mergeClassNames,
  boxMarginPaddingGenerator,
} from "../../utils/helpers/stringFormat";
import useMeasure from "../../utils/hooks/useMeasure";
import InputBase from "./InputBase";
import TextFieldOutlinedBase from "./Outlined";
const TextFieldBase = styled.div();
type Props = {
  variant?: "outlined" | "flat";
  startAdornment?: React.ReactNode;
  endAdornment?: React.ReactNode;
  tag?: "div" | "input" | "textarea";
  size?: number;
  label?: string;
};
const TextField: React.FC<Props> = ({
  variant = "flat",
  startAdornment,
  endAdornment,
  tag = "input",
  size = 16,
  label = "TextField",
}) => {
  const theme = useTheme();
  const [startAdornmentRef, { width: startAdornmentWidth }] = useMeasure<
    HTMLDivElement
  >();
  const [endAdornmentRef, { width: endAdornmentWidth }] = useMeasure<
    HTMLDivElement
  >();

  return (
    <TextFieldBase
      className={mergeClassNames(
        theme.prefix + "-textfield",
        theme.prefix + "-textfield-" + variant
      )}
      css={{
        "--text-field-input-base-padding": size + "px",
        "--text-field-adornment-start-width": startAdornmentWidth + "px",
        "--text-field-adornment-end-width": endAdornmentWidth + "px",
      }}
    >
      {startAdornment && (
        <div
          className={theme.prefix + "-textfield-start-adornment"}
          ref={startAdornmentRef}
        >
          {startAdornment}
        </div>
      )}
      <InputBase
        as={tag}
        className={theme.prefix + "-input-base"}
        placeholder=" "
        {...(tag === "div" ? { contentEditable: true } : {})}
      />

      <TextFieldOutlinedBase label={label} prefix={theme.prefix} />

      {endAdornment && (
        <div
          className={theme.prefix + "-textfield-end-adornment"}
          ref={endAdornmentRef}
        >
          {endAdornment}
        </div>
      )}
      {/* </label> */}
    </TextFieldBase>
  );
};

export default TextField;
